<template>
    <div id="elementT">
        <el-table
                ref="multipleTable"
                :data="curElementList"
                tooltip-effect="dark"
                style="width: 100%"
                :row-class-name="tableRowClassName"
                :header-cell-style="headerCellStyle"
                :cell-style="cellStyle"
                @select="handleSelectionChange"
                @select-all="selectAllFn"
                @filter-change="filterChangeFn"
        >
            <el-table-column
                    type="selection"
                    width="55">
            </el-table-column>
            <el-table-column
                    prop="index"
                    label="序号"
                    width="80"
                    sortable
            >
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="材料清单"
                    width="320"
            >
            </el-table-column>
            <el-table-column
                    prop="require"
                    label="材料准备要求"
                    width="200">
            </el-table-column>
            <el-table-column
                    prop="permit"
                    label="审查要点"
                    min-width="300"
                    >
            </el-table-column>
            <el-table-column
                    prop="steps"
                    label="需查看的流程"
                    width="280"
                    column-key="filterTag"
                    align="center"
                    :filters="filtersList"
                    :filter-method="filterTag"
                    filter-placement="bottom-end"
                    :filtered-value="filteredList"
            >
                <template slot-scope="scope">
                    <el-tag v-show="scope.row.steps.length" size="medium">{{ scope.row.steps.join('-') }}</el-tag>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
export default {
  name: 'ServiceElement',
  data () {
    return {
      userIfon: {},
      curElementList: [],
      multipleSelection: [],
      curSeleckRow: [],
      selectionElementId: [],
      curMatter: '',
      allID: [],
      filtersList: [{ text: '接件', value: '接件' }, { text: '经办人', value: '经办人' }, { text: '踏勘中心', value: '踏勘中心' }],
      filteredList: []
    }
  },
  watch: {

  },
  created: function () {
    if (sessionStorage.userInfo) {
      this.userIfon = JSON.parse(sessionStorage.userInfo)
    } else {
      console.log('call-无用户信息')
    }
    let self = this
    if (sessionStorage.reference) {
      let obj = JSON.parse(sessionStorage.reference)
      this.curMatter = obj.curMatter
      this.selectionElementId = obj.selectionElementId
      this.filteredList = obj.filteredList
      if (obj.curTheme) {
        if (obj.curMatter) {
          this.getElement()
        } else {
          self.$alert('未选择事项', '警告', {
            confirmButtonText: '确定',
            callback: action => {
            }
          })
        }
      } else {
        self.$alert('未选择主题', '警告', {
          confirmButtonText: '确定',
          callback: action => {
          }
        })
      }
    } else {}
  },
  mounted: function () {
  },
  updated: function () {
    console.log(12321)
    let rows = []
    this.curSeleckRow = []
    this.toggleSelection(rows)
    for (let i = 0; i < this.curElementList.length; i++) {
      if (this.selectionElementId.indexOf('' + this.curElementList[i].id) > -1) {
        rows.push(this.curElementList[i])
        this.curSeleckRow.push(this.curElementList[i].index)
      }
    }
    this.toggleSelection(rows)
  },
  methods: {
    async getElement () {
      try {
        let params = {
          item: this.curMatter
        }
        let res = await this.$api.unsplash.postElements(params)
        if (res.errno === '0') {
          let temp = res.data
          this.allID = []
          for (let i = 0; i < temp.length; i++) {
            temp[i].index = i + 1
            // temp[i].steps = ['接件', '科室负责人', '接件', '科室负责人']
            this.allID.push(temp[i].id)
          }
          if (temp[0].process.length) {
            let FL = temp[0].process.split('-')
            this.filtersList = []
            for (let i = 0; i < FL.length; i++) {
              this.filtersList.push({
                text: FL[i],
                value: FL[i]
              })
            }
          } else {
            this.filtersList = []
          }
          this.curElementList = temp
          let referenceObj = JSON.parse(sessionStorage.reference)
          referenceObj.allID = this.allID
          sessionStorage.reference = JSON.stringify(referenceObj)
          console.log(temp)
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async reviewClickFn (aa) {
      try {
        let params = aa
        let res = await this.$api.unsplash.reviewClick(params)
        console.log(res)
        if (res.errno === '0') {
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    headerCellStyle ({ row, column, rowIndex, columnIndex }) {
      if (rowIndex > -1) {
        return {
          'text-align': 'center'
        }
      }
    },
    cellStyle ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0 || columnIndex === 1) {
        return {
          'text-align': 'center'
        }
      } else if (columnIndex === 1) {
        return {
        }
      }
    },
    handleSelectionChange (selection, row) { // 单击
      this.multipleSelection = selection
      this.curSeleckRow = []
      this.selectionElementId = []
      for (let i = 0; i < this.multipleSelection.length; i++) {
        this.curSeleckRow.push(this.multipleSelection[i].index)
        this.selectionElementId.push('' + this.multipleSelection[i].id)
      }
      // let temp = {
      //   selectionElementId: this.selectionElementId,
      //   allID: this.allID
      // }
      // sessionStorage.selectionElementId = JSON.stringify(temp)
      let referenceObj = JSON.parse(sessionStorage.reference)
      referenceObj.selectionElementId = this.selectionElementId
      referenceObj.allID = this.allID
      sessionStorage.reference = JSON.stringify(referenceObj)
      if (this.userIfon.role === '大厅') {
        var EleClickObj = {
          itemreviews: this.curElementList,
          current_click: row.id,
          reviewsclick: this.selectionElementId.join('-'),
          lingpai: localStorage.lingpai,
          user_id: sessionStorage.user_id
        }
        console.log(12, EleClickObj)
        this.reviewClickFn(EleClickObj)
      }
    },
    selectAllFn (selection) { // 全选
      this.multipleSelection = selection
      this.curSeleckRow = []
      this.selectionElementId = []
      for (let i = 0; i < this.multipleSelection.length; i++) {
        this.curSeleckRow.push(this.multipleSelection[i].index)
        this.selectionElementId.push('' + this.multipleSelection[i].id)
      }
      // let temp = {
      //   selectionElementId: this.selectionElementId,
      //   allID: this.allID
      // }
      // sessionStorage.selectionElementId = JSON.stringify(temp)
      let referenceObj = JSON.parse(sessionStorage.reference)
      referenceObj.selectionElementId = this.selectionElementId
      referenceObj.allID = this.allID
      sessionStorage.reference = JSON.stringify(referenceObj)
      if (this.userIfon.role === '大厅') {
        var EleClickObj = {
          itemreviews: this.curElementList,
          current_click: Math.min.apply(null, this.allID),
          reviewsclick: this.selectionElementId.join('-'),
          lingpai: localStorage.lingpai,
          user_id: sessionStorage.user_id
        }
        console.log(13, EleClickObj)
        this.reviewClickFn(EleClickObj)
      }
    },
    tableRowClassName ({row, rowIndex}) {
      // console.log(11, row)
      // console.log(22, rowIndex)
      if (this.selectionElementId.indexOf('' + row.id) > -1) {
        return 'warning-row-element'
      }
      return ''
    },
    toggleSelection (rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row)
        })
      } else {
        this.$refs.multipleTable.clearSelection()
      }
    },
    filterTag (value, row) {
      return row.steps.indexOf(value) > -1
    },
    filterChangeFn (filters) {
      let referenceObj = JSON.parse(sessionStorage.reference)
      referenceObj.filteredList = filters.filterTag
      sessionStorage.reference = JSON.stringify(referenceObj)
    },
    notifyFn (title, msg) {
      this.$notify({
        title: title,
        message: msg,
        type: 'warning',
        dangerouslyUseHTMLString: true,
        duration: 5000,
        position: 'top-right'
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
    #elementT{
        .elementT .el-table{
            font-size: 16px;
            .cell{
                text-align: center;
            }
        }
        .el-table .warning-row-element {
            background: rgba(253, 113, 20, 0.8);
            color: rgba(0, 0, 0, 0.92);
        }
    }

</style>



